import React, { Component } from 'react'
import { Button, Modal, Divider } from 'antd'
import { connect } from 'dva'
import { saveRef } from '@/utils/utils'
import withRef from '@/HocComponent/withRef'

import BaseContent from '@/components/BaseContent'
import BaseTable from '@/components/BaseTable'

import styles from './style.module.less'

const TABLE_KEY = 'identity_id';

const mapState = store => {
  return { ...store.role }
};

/**
 * modal
 * @prop {function} onCreateComplete - 
 * @prop {function} onEditComplete - 
 * @method create - 
 * @method edit - 
 */
@connect(mapState)
@withRef
class AddModal extends Component {

  constructor(props) {
    super(props);

    this.type = {
      CREATE: Symbol('create'),
      MODIFY: Symbol('modify'),
    };

    this.state = {
      type: this.type.CREATE,
      visible: false
    };
  }

  create() {
    this.setState({
      type: this.type.CREATE,
      visible: true
    });
  }

  edit() {
    this.setState({
      type: this.type.MODIFY,
      visible: true
    });
  }

  handleOk = () => {
    console.log()
    const value = this.refs.roleInput.value

    this.props.dispatch({type: 'role/addRole', identity_text: value}).then(() => {
      console.log('')
    })
    this.setState({
      visible: false
    });
  }

  handleCancel = (value) => {
    console.log('')
    
    this.setState({
      visible: false
    });
  }

  render() {
    return (
      <Modal
        visible={this.state.visible}
        width={600}
        footer={[
          <Button size="large" key="save" type="primary" onClick={this.handleOk}></Button>,
          <Button size="large" key="cancel" onClick={this.handleCancel}></Button>,
        ]}
        bodyStyle={{
          padding: '24px 80px',
        }}
      >
        <div className={styles.addModal}>
          <h2 className={styles.title}>
            {
              this.state.type === this.type.CREATE ? '' : ''
            }
          </h2>
          <div>
            <input type="text" ref='roleInput' className={styles.input} placeholder="" />
          </div>
        </div>
      </Modal>
    )
  }
}


/**
 * modal
 * @prop {function} onCreateComplete - 
 * @prop {function} onEditComplete - 
 * @method create - 
 * @method edit - 
 */
@connect(mapState)
@withRef
class EditAuthorityApiModal extends Component {

  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      identity: {}
    };
  }

  edit (identity) {
    console.log(identity)
    this.setState({
      visible: true,
      identity: identity
    });
  }

  handleOk = () => {
    const value = this.refs.roleInput.value

    this.props.dispatch({type: 'role/addRole', identity_text: value}).then(() => {
      console.log('')
    })
    this.setState({
      visible: false
    });
  }

  handleCancel = (value) => {
    console.log('')
    
    this.setState({
      visible: false
    });
  }

  render() {
    const {identity_text} = this.state.identity
    return (
      <Modal
        visible={this.state.visible}
        width={600}
        footer={[
          <Button size="large" key="save" type="primary" onClick={this.handleOk}></Button>,
          <Button size="large" key="cancel" onClick={this.handleCancel}></Button>,
        ]}
        bodyStyle={{
          padding: '24px 80px',
        }}
      >
        <div className={styles.addModal}>
          <h2 className={styles.title}>
            {identity_text}
          </h2>
          <div>
            

          </div>
        </div>
      </Modal>
    )
  }
}

@connect(mapState)
class Role extends Component {
  componentDidMount() {
    this.props.dispatch({ type: 'role/fetch' })
  }

  handleCreate = () => {
    // console.log(this.addModal)
    this.addModal.create()
  }

  handleEdit = () => {
    this.addModal.edit()
  }

  handleEditAuthorityApiModal = (identity) => {
    this.editAuthorityApiModal.edit(identity)
  }

  getColumns() {
    const columns = [{
      title: 'ID',
      dataIndex: 'identity_id',
      key: 'identity_id',
    }, {
      title: '',
      dataIndex: 'identity_text',
      key: 'identity_text',
    }, {
      title: '',
      key: 'action',
      render: (item) => {
        return (
          <React.Fragment>
            <a href="###"></a>
            <Divider type="vertical" />
            <a href="javascript:;" onClick={() => this.handleEditAuthorityApiModal(item)}></a>
            <Divider type="vertical" />
            <a href="javascript:;" onClick={this.handleEdit}></a>
            <Divider type="vertical" />
            <a href="javascript:;" ></a>
          </React.Fragment>
        );
      },
    }];
    return columns
  }

  addRole () {
    
  }

  render() {
    const { table } = this.props
    console.log(table)

    return (
      <BaseContent>
        <React.Fragment>
          <BaseTable
            columns={this.getColumns()}
            buttons={
              [
                <Button
                  type="primary"
                  icon="plus"
                  size="large"
                  onClick={this.handleCreate}
                ></Button>
              ]
            }
            rowKey={TABLE_KEY}
            dataSource={table.data}
            wrapStyle={{
              padding: 0,
            }}
          />
        </React.Fragment>
        <AddModal wrappedComponentRef={(component) => this.addModal = component} />
        <EditAuthorityApiModal wrappedComponentRef={(component) => this.editAuthorityApiModal = component} />
      </BaseContent>
    )
  }
}

export default Role
